header {
  position: relative;

  background-color: white;

  padding: 1rem;

  border-bottom: 1px solid black;

  .hideInput {
    visibility: hidden;
  }

  #inputSearch {
    margin-left: auto;
    width: 20%;
  }

  .logo {
    letter-spacing: 0.25rem;
    font-size: 1.5rem;
  }

  .logo.toggle {
    display: none;
  }

  .nav-links {
    display: flex;

    li {
      list-style: none;
      padding: 0.75em;
      
    }

    a {
      text-decoration: none;
   
    }
  }
 

  .bottomLinks {
    display: none;
  }

  .burger {
    display: none;
    background-color: transparent;
    border: 0;

    div {
      width: 1.75rem;
      height: 0.25rem;
      background-color: black;
      margin: 0.25rem;
      transition: transform 0.3s ease-in;
    }
  }

  .toggle #line1 {
    transform: rotate(-45deg) translate(-5px, 6px);
  }

  .toggle #line2 {
    opacity: 0;
  }

  .toggle #line3 {
    transform: rotate(45deg) translate(-5px, -6px);
  }

  .mobileHide {
    display: block;
  }
  .desktopHide {
    display: none;
  }
  .headerOuter {
    display: flex;
    justify-content: center;
    justify-content: space-between;
    width: 90vw;
  }

  .endHeader {
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      padding: 0 0.37em;
    }
  }
}
