.heroBanner{
    display: flex;
    padding: 1em;
    .heroInnerBanner{
        text-align: center;
        
        color: white;
        padding: 1em;
        flex: 1;
        background-color: black;
        div{
            margin: auto;
            height: 0.5rem;
            
            width: 5rem;
            background-color: white;
            border-radius: 0.25rem;

        }

    }
    img{
        flex: 2;
        padding: 1em 1em 1em 0;
    }
    
}


